              <li><strong>è½¯ç‚¹æ•°:</strong> æœ‰AæŒ‰11ç‚¹è®¡ç®—çš„æ€»ç‚¹æ•°</li>
              <li><strong>BlackJack:</strong> å‰ä¸¤å¼ ç‰Œå°±è¾¾åˆ°21ç‚¹</li>
