<script>
export default {
  name: 'BlackjackView',
  metaInfo: {
    title: '21ç‚¹è®¡ç®—å™¨ - 21ç‚¹æ¸¸æˆç‚¹æ•°è®¡ç®—å·¥å…·',
    meta: [
      { name: 'description', content: '21ç‚¹æ¸¸æˆè®¡ç®—å™¨ï¼Œè‡ªåŠ¨è®¡ç®—æœ€ä½³ç‚¹æ•°ï¼Œå¤„ç†Aç‰Œçš„1ç‚¹æˆ–11ç‚¹é€‰æ‹©ï¼Œæä¾›æ¸¸æˆç­–ç•¥å»ºè®®ã€‚' },
      { name: 'keywords', content: '21ç‚¹,BlackJack,,ç‚¹æ•°è®¡ç®—,æ¸¸æˆå·¥å…·,ç­–ç•¥åˆ†æ' }
    ]
  },
  data() {
    return {
      playerCards: [],
      availableCards: [
        { value: 1, display: 'A', count: 0 },
        { value: 2, display: '2', count: 0 },
        { value: 3, display: '3', count: 0 },
        { value: 4, display: '4', count: 0 },
        { value: 5, display: '5', count: 0 },
        { value: 6, display: '6', count: 0 },
        { value: 7, display: '7', count: 0 },
        { value: 8, display: '8', count: 0 },
        { value: 9, display: '9', count: 0 },
        { value: 10, display: '10', count: 0 },
        { value: 10, display: 'J', count: 0 },
        { value: 10, display: 'Q', count: 0 },
        { value: 10, display: 'K', count: 0 }
      ]
    }
  },
  computed: {
    aceCount() {
      return this.playerCards.filter(card => card.value === 1).length
    },
    
    hardTotal() {
      return this.playerCards.reduce((sum, card) => sum + card.value, 0)
    },
    
    softTotal() {
      let total = this.hardTotal
      let aces = this.aceCount
      
      // å°½å¯èƒ½å¤šçš„AæŒ‰11ç‚¹è®¡ç®—ï¼Œä½†ä¸èƒ½çˆ†ç‰Œ
      while (aces > 0 && total + 10 <= 21) {
        total += 10
        aces--
      }
      
      return total
    },
    
    bestScore() {
      return this.softTotal
    },
    
    alternativeScore() {
      if (this.aceCount > 0 && this.softTotal !== this.hardTotal) {
        return this.hardTotal
      }
      return null
    },
    
    bustProbability() {
      if (this.playerCards.length === 0) return 0
      if (this.bestScore >= 21) return this.bestScore > 21 ? 100 : 0
      
      // ç®€åŒ–çš„çˆ†ç‰Œæ¦‚ç‡è®¡ç®—
      const bustCards = Math.max(0, 13 - (21 - this.bestScore))
      return Math.round((bustCards / 13) * 100)
    }
  },
  methods: {
    addCard(cardType) {
      if (cardType.count >= 4) {
        this.$message.warning('è¯¥ç‰Œå·²ç”¨å®Œï¼ˆæ¯ç§ç‰Œæœ€å¤š4å¼ ï¼‰')
        return
      }
      
      const newCard = {
        value: cardType.value,
        display: cardType.display
      }
      
      this.playerCards.push(newCard)
      cardType.count++
    },
    
    removeCard(index) {
      const removedCard = this.playerCards[index]
      this.playerCards.splice(index, 1)
      
      // æ¢å¤å¯ç”¨ç‰Œæ•°
      const cardType = this.availableCards.find(card => 
        card.value === removedCard.value && card.display === removedCard.display
      )
      if (cardType) {
        cardType.count--
      }
    },
    
    clearCards() {
      this.playerCards = []
      this.availableCards.forEach(card => {
        card.count = 0
      })
    },
    
    newGame() {
      this.clearCards()
      this.$message.success('æ–°æ¸¸æˆå¼€å§‹ï¼')
    },
    
    getScoreClass() {
      if (this.bestScore === 21) return 'perfect'
      if (this.bestScore > 21) return 'bust'
      if (this.bestScore >= 17) return 'good'
      return 'normal'
    },
    
    getScoreLabel() {
      if (this.playerCards.length === 0) return 'è¯·æ·»åŠ ç‰Œ'
      if (this.bestScore === 21 && this.playerCards.length === 2) return 'BlackJack!'
      if (this.bestScore === 21) return '21ç‚¹!'
      if (this.bestScore > 21) return 'çˆ†ç‰Œ!'
      return 'ç‚¹æ•°'
    },
    
    getStatusTitle() {
      if (this.playerCards.length === 0) return 'ç­‰å¾…å¼€å§‹'
      if (this.bestScore === 21 && this.playerCards.length === 2) return 'BlackJack!'
      if (this.bestScore === 21) return '21ç‚¹å®Œç¾!'
      if (this.bestScore > 21) return 'çˆ†ç‰Œäº†!'
      if (this.bestScore >= 17) return 'ç‚¹æ•°ä¸é”™'
      return 'ç»§ç»­æ¸¸æˆ'
    },
    
    getStatusType() {
      if (this.playerCards.length === 0) return 'info'
      if (this.bestScore === 21) return 'success'
      if (this.bestScore > 21) return 'error'
      if (this.bestScore >= 17) return 'warning'
      return 'info'
    },
    
    getStatusDescription() {
      if (this.playerCards.length === 0) return 'é€‰æ‹©ç‰Œé¢å¼€å§‹æ¸¸æˆ'
      if (this.bestScore === 21 && this.playerCards.length === 2) return 'å‰ä¸¤å¼ ç‰Œå°±è¾¾åˆ°21ç‚¹ï¼Œè¿™æ˜¯æœ€ä½³ç»“æœï¼'
      if (this.bestScore === 21) return 'è¾¾åˆ°21ç‚¹ï¼Œä¸èƒ½å†è¦ç‰Œäº†'
      if (this.bestScore > 21) return 'ç‚¹æ•°è¶…è¿‡21ï¼Œæ¸¸æˆç»“æŸ'
      if (this.bestScore >= 17) return 'ç‚¹æ•°å·²ç»æ¯”è¾ƒé«˜ï¼Œè¦ç‰Œéœ€è°¨æ…'
      return 'ç‚¹æ•°è¿˜æ¯”è¾ƒä½ï¼Œå¯ä»¥è€ƒè™‘ç»§ç»­è¦ç‰Œ'
    },
    
    getStrategyRecommendation() {
      if (this.playerCards.length === 0) return 'å¼€å§‹æ¸¸æˆ'
      if (this.bestScore >= 21) return 'åœç‰Œ'
      if (this.bestScore >= 17) return 'å»ºè®®åœç‰Œ'
      if (this.bestScore <= 11) return 'å»ºè®®è¦ç‰Œ'
      if (this.aceCount > 0 && this.bestScore <= 17) return 'è½¯ç‰Œï¼Œå»ºè®®è¦ç‰Œ'
      return 'è°¨æ…è€ƒè™‘'
    },
    
    getStrategyIcon() {
      if (this.playerCards.length === 0) return 'el-icon-info'
      if (this.bestScore >= 21) return 'el-icon-close'
      if (this.bestScore >= 17) return 'el-icon-warning'
      if (this.bestScore <= 11) return 'el-icon-check'
      return 'el-icon-question'
    },
    
    getStrategyDetails() {
      const details = []
      
      if (this.playerCards.length === 0) {
        details.push('é€‰æ‹©ç‰Œé¢å¼€å§‹æ¸¸æˆ')
        return details
      }
      
      if (this.bestScore > 21) {
        details.push('å·²ç»çˆ†ç‰Œï¼Œæ¸¸æˆç»“æŸ')
        return details
      }
      
      if (this.bestScore === 21) {
        details.push('å·²è¾¾åˆ°æœ€ä½³ç‚¹æ•°21ç‚¹')
        if (this.playerCards.length === 2) {
          details.push('BlackJackï¼å‰ä¸¤å¼ ç‰Œå°±è¾¾åˆ°21ç‚¹')
        }
        return details
      }
      
      if (this.aceCount > 0) {
        details.push(`æœ‰${this.aceCount}å¼ Aç‰Œï¼Œæä¾›äº†çµæ´»æ€§`)
        if (this.softTotal !== this.hardTotal) {
          details.push('å½“å‰æ˜¯è½¯ç‰Œï¼ˆAæŒ‰11ç‚¹è®¡ç®—ï¼‰')
        }
      }
      
      if (this.bestScore >= 17) {
        details.push('ç‚¹æ•°å·²ç»è¾ƒé«˜ï¼Œè¦ç‰Œé£é™©å¢å¤§')
        details.push(`çˆ†ç‰Œæ¦‚ç‡çº¦ä¸º ${this.bustProbability}%`)
      } else if (this.bestScore <= 11) {
        details.push('ç‚¹æ•°è¾ƒä½ï¼Œè¦ç‰Œä¸ä¼šçˆ†ç‰Œ')
        details.push('å»ºè®®ç»§ç»­è¦ç‰Œ')
      } else {
        details.push('å¤„äºä¸­ç­‰ç‚¹æ•°åŒºé—´')
        details.push(`çˆ†ç‰Œæ¦‚ç‡çº¦ä¸º ${this.bustProbability}%`)
      }
      
      return details
    }
  }
}
</script>

